import React from 'react';
import '../assets/scss/main.scss';
import Helmet from 'react-helmet';

import Header from '../components/Header';
import Main from '../components/Main';
import Footer from '../components/Footer';
import favicon from '../static/favicon.ico';

const pushState = (obj, url) => {
  if(typeof history !== 'undefined') {
    history.pushState(obj, '', url);
  }
}

const onPopState = (handler) => {
  if(typeof window !== 'undefined') {
    window.onpopstate = handler;
  }
}

class Template extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isArticleVisible: false,
      timeout: false,
      articleTimeout: false,
      article: '',
      loading: 'is-loading'
    }
    this.handleOpenArticle = this.handleOpenArticle.bind(this);
    this.handleCloseArticle = this.handleCloseArticle.bind(this);
    this.handleArticleTrans = this.handleArticleTrans.bind(this);
  }

  componentDidMount() {
    this.timeoutId = setTimeout(() => {
        this.setState({loading: ''});
    }, 100);

    onPopState((event) => {
      if(event.state) {
        if(this.state.article === "" || this.props.location.hash === "") {
          this.handleOpenArticle(event.state.article, false);
        } else {
          this.handleArticleTrans(event.state.article, false);
        }
      }
    });

    if(this.props.location.pathname === '/') {
      switch(this.props.location.hash) {
        case '#intro':
          this.handleOpenArticle('intro');
          break;
        case '#work':
          this.handleOpenArticle('work');
          break;
        case '#about':
          this.handleOpenArticle('about');
          break;
        case '#blog':
          this.handleOpenArticle('blog');
          break;
        case '#contact':
          this.handleOpenArticle('contact');
          break;
        default:
          // this will cause problem when backing out of the site
          pushState({article: ''}, '/');
      }
    }
  }

  componentWillUnmount() {
    if (this.timeoutId) {
        clearTimeout(this.timeoutId);
    }

    onPopState(null);
  }

  handleOpenArticle(article, manuallyPushHistory=true) {
    if(manuallyPushHistory) {
      pushState({article}, `/#${article}`);
    }

    // toggle background zooming effect
    this.setState({
      isArticleVisible: !this.state.isArticleVisible,
      article
    })

    // toggle component display property
    setTimeout(() => {
      this.setState({
        timeout: !this.state.timeout
      })
    }, 325)

    // trigger article display animation
    setTimeout(() => {
      this.setState({
        articleTimeout: !this.state.articleTimeout
      })
    }, 350)
  }

  handleCloseArticle() {
    pushState({article: ''}, `/`);

    // trigger article display animation
    this.setState({
      articleTimeout: !this.state.articleTimeout
    })

    // toggle component display property
    setTimeout(() => {
      this.setState({
        timeout: !this.state.timeout
      })
    }, 325)

    // toggle background zooming effect
    setTimeout(() => {
      this.setState({
        isArticleVisible: !this.state.isArticleVisible,
        article: ''
      })
    }, 350)
  }

  handleArticleTrans(article, manuallyPushHistory=true) {
    if(manuallyPushHistory) {
      pushState({article}, `/#${article}`);
    }

    // trigger article display animation
    this.setState({
      articleTimeout: !this.state.articleTimeout
    });

    // set article
    setTimeout(() => {
      this.setState({
        article
      })
    }, 325);

    // trigger article display animation
    setTimeout(() => {
      this.setState({
        articleTimeout: !this.state.articleTimeout
      })
    }, 350);
  }

  render() {
    const siteTitle = this.props.data.site.siteMetadata.title
    const siteDescription = this.props.data.site.siteMetadata.description
    const { location, children } = this.props

    let rootPath = `/`

    let content;

    if (location.pathname === rootPath) {
      content = (
        <div id="wrapper">
          <Header onOpenArticle={this.handleOpenArticle} timeout={this.state.timeout} />
          <Main
            isArticleVisible={this.state.isArticleVisible}
            timeout={this.state.timeout}
            articleTimeout={this.state.articleTimeout}
            article={this.state.article}
            onCloseArticle={this.handleCloseArticle}
            onArticleTrans={this.handleArticleTrans}
            blogData={this.props.data.allMarkdownRemark.edges}
          />
          <Footer timeout={this.state.timeout} />
        </div>
      )
    } else {
      content = (
        <div id="wrapper" className="page">
          <div id="other-pages">
            {children({
              ...this.props,
              article: this.state.article,
              onOpenArticle: this.handleOpenArticle, 
              articleTimeout: this.state.articleTimeout,
              onCloseArticle: this.handleCloseArticle,
              blogData: this.props.data.allMarkdownRemark.edges
            })}
          </div>
        </div>
      )
    }

    return (
      <div className={`body ${this.state.loading} ${this.state.isArticleVisible ? 'is-article-visible' : ''}`}>
        <Helmet>
            <title>{siteTitle}</title>
            <meta name="description" content={siteDescription} />
            <link key="icon" rel="icon" href={favicon} />
        </Helmet>

        {content}

        <div id="bg"></div>
      </div>
    )
  }
}

export default Template;

export const pageQuery = graphql`
  query PageQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          excerpt
          timeToRead
        }
      }
    }
  }
`;


