import React from 'react';
import PropTypes from 'prop-types';

const Header = (props) => (
    <header id="header" style={props.timeout ? {display: 'none'} : {}}>
        <div className="logo">
            <span className="icon fa-terminal"></span>
        </div>
        <div className="content">
            <div className="inner">
                <h1>David's</h1>
                <p>
                    Hi there, welcome to my site. I hope you find it interesting.<br /> Feel free to contact me if you want to know more about me or just want a chat!
                </p>
            </div>
        </div>
        <nav>
            <ul>
                <li><a href="/#intro" onClick={() => {props.onOpenArticle('intro')}}>Intro</a></li>
                <li><a href="/#work" onClick={() => {props.onOpenArticle('work')}}>Work</a></li>
                <li><a href="/#about" onClick={() => {props.onOpenArticle('about')}}>About</a></li>
                <li><a href="/#blog" onClick={() => {props.onOpenArticle('blog')}}>Blog</a></li>
                <li><a href="/#contact" onClick={() => {props.onOpenArticle('contact')}}>Contact</a></li>
            </ul>
        </nav>
    </header>
)

Header.propTypes = {
    onOpenArticle: PropTypes.func,
    timeout: PropTypes.bool
}

export default Header


